import axios from "axios";

const BASE_URL = process.env.REACT_APP_DTEC_API_ENDPOINT;
const BASE_URL_CLAF = process.env.REACT_APP_CLAF_API_ENDPOINT;
// let url = 'http://192.168.10.201:8092'
// let url = 'http://132.247.164.12:8092'

const apiDtec = axios.create({
  baseURL: BASE_URL,
  responseType: "json",
  headers: { Accept: "*/*", "Content-Type": "application/json;charset=utf-8" },
});

apiDtec.interceptors.request.use((config) => {
  const token = window.sessionStorage.getItem("token");
  if (token) {
    config.headers["Authorization"] = `Bearer ${token}`;
  }
  return config;
});

const apiClaf = axios.create({
  baseURL: BASE_URL_CLAF,
  responseType: "json",
  headers: { Accept: "*/*", "Content-Type": "application/json;charset=utf-8" },
});

apiClaf.interceptors.request.use((config) => {
  const token = window.sessionStorage.getItem("token");
  if (token) {
    config.headers["Authorization"] = `Bearer ${token}`;
  }
  return config;
});

export default {
  apiClaf,
  apiDtec,
};
