import React, { useContext } from "react";
import { BrowserRouter, Switch } from "react-router-dom";
import { Context } from "./Context";
import { Login, Home, SolicitudResguardo, SolicitudTitulos } from "./pages/";
import { Redirect } from "react-router-dom";
import PublicRoute from "./hooks/PublicRoute";
import PrivateRoute from "./hooks/PrivateRoute";

function AppRouter() {
  const { isAuth, removeAuth } = useContext(Context);

  return (
    <BrowserRouter>
      <Switch>
        <PublicRoute restricted={true} component={Login} path="/" exact />
        <PrivateRoute component={Home} path="/home" exact />
        <PrivateRoute component={SolicitudResguardo} path="/solicitud" exact />
        <PrivateRoute
          component={SolicitudTitulos}
          path="/solicitud/titulos"
          exact
        />
      </Switch>
    </BrowserRouter>
  );
}

export default AppRouter;
